var React = require('react/addons');
var Router = require('react-router');
var {DefaultRoute, Link, Route, RouteHandler} = Router;
var {Highlight, Markdown} = require('../src/markdocs.js');
var throttle = require('lodash.throttle');

var defaultTheme = {
  blockBackground: '#F8F5EC',
  baseColor: '#5C6E74',
  commentColor: '#93A1A1',
  punctuationColor: '#999999',
  propertyColor: '#990055',
  selectorColor: '#669900',
  operatorColor: '#a67f59',
  operatorBg: '#FFFFFF',
  variableColor: '#ee9900',
  functionColor: '#DD4A68',
  keywordColor: '#0077aa',
  selectedColor: '#b3d4fc',
  inlineCodeColor: '#DB4C69',
  inlineCodeBackground: '#F9F2F4',
  highlightBackground: '#F7EBC6',
  highlightAccent: '#F7D87C'
};

var colorClasses = Object.keys(defaultTheme);

var lessString = require('raw!../src/markdocs.less');

function createColorVariableString(colors) {
  return colorClasses.map(color => {
    return `@${color}: ${colors[color]};`;
  }).join('\n');
}

function renderStyles(colors, callback) {
  var str = lessString;
  str = str + '\n\n' + createColorVariableString(colors);
  less.render(str)
    .then(function (output) {
      callback(output.css);
    }, function (err) {
      console.log(err);
    });
}

var App = React.createClass({displayName: "App",
  render: function () {
    return (
      React.createElement("div", {className: "container"}, 
        React.createElement(RouteHandler, null)
      )
    );
  }
});

var Home = React.createClass({displayName: "Home",
  mixins: [React.addons.LinkedStateMixin],
  getInitialState: function () {
    return {
      colors: defaultTheme,
      css: ''
    };
  },
  componentDidMount: function () {

    this.updateColors = throttle(() => {
      renderStyles(this.state.colors, (css) => {
        this.setState({css});
      });
    }, 500);

    // this.updateColors();

  },
  updateSwatch: function (color) {
    return (e) => {
      var colors = this.state.colors;
      colors[color] = e.target.value;
      this.setState({colors});
      this.updateColors();
    };
  },
  render: function () {
    return (React.createElement("div", {className: "docs"}, 
      React.createElement("style", null, this.state.css), 

      React.createElement("div", {className: "color-hacker"}, 
        React.createElement("div", {className: "sidebar"}, 
          React.createElement("h2", {className: "download"}, 
            React.createElement("span", null, React.createElement("a", {href: "http://prismjs.com", target: "_blank"}, "Prism.js"), " Theme Generator"), 
            React.createElement("a", {hidden: !this.state.css, className: "btn", href: 'data:application/octet-stream;charset=utf-8,' + encodeURI(this.state.css)}, "Download CSS")
          ), 
          React.createElement("div", {className: "color-wrapper"}, colorClasses.map(color => {
            return (React.createElement("div", {className: "color"}, 
              React.createElement("div", {className: "color-demo", style: {background: this.state.colors[color]}}, 
                 React.createElement("input", {type: "color", value: this.state.colors[color], onChange: this.updateSwatch(color)})
              ), 
              React.createElement("code", null, "@", color), 
              React.createElement("input", {type: "type", value: this.state.colors[color], onChange: throttle(this.updateSwatch(color), 100)})
            ));
          }))
        ), 
        React.createElement("div", {className: "sample"}, 
          React.createElement(Markdown, {prism: true, source: require('./colors-demo.md')})
        )
      )
    ));
  }
});

var Tests = React.createClass({displayName: "Tests",
  getInitialState: function () {
    return {
      foo: 'abc'
    };
  },
  render: function () {
    return (React.createElement("div", null, 
      React.createElement("input", {valueLink: this.linkState('foo')}), 

      React.createElement(Highlight, {className: "language-jsx"}, 
      `<foo>${this.state.foo}</foo>`
      ), 
      React.createElement("pre", {className: "language-jsx"}, 
      `<foo />`
      )
    ));
  }
});

var routes = (
  React.createElement(Route, {name: "app", path: "/", handler: App}, 
    React.createElement(DefaultRoute, {handler: Home})
  )
);

Router.run(routes, function (Handler) {
  React.render(React.createElement(Handler, null), document.getElementById('app'));
});



